@use 'variables'as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    font-family: v.$fontPrincipal;
    font-size: 1.6rem;
    font-weight: 300;
    background-color: v.$gray;
    color: v.$black;

    &::-webkit-scrollbar {
        background-color: v.$gray;
        width: .8rem;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 1rem;
        background-color: v.$darkGray;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

.container {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}

.container-m {
    width: 95%;
    max-width: 800px;
    margin: 0 auto;
}

.container-s {
    width: 95%;
    max-width: 500px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
}

img {
    max-width: 100%;
}