/** Media Queries **/
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
  background-color: rgba(232, 238, 243, 0.8549019608);
  color: #2d3137;
}
body::-webkit-scrollbar {
  background-color: rgba(232, 238, 243, 0.8549019608);
  width: 0.8rem;
}
body::-webkit-scrollbar-thumb {
  border-radius: 1rem;
  background-color: #58585c;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-m {
  width: 95%;
  max-width: 800px;
  margin: 0 auto;
}

.container-s {
  width: 95%;
  max-width: 500px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
}

h1,
h2,
h3 {
  font-family: "Poppins", sans-serif;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.2rem;
}

h3 {
  font-size: 1.8rem;
  text-align: center;
}

p {
  margin: 0;
  font-size: 1.6rem;
}

.header {
  padding: 5rem 1rem 10rem 1rem;
  background-color: #22458c;
  color: #ffffff;
}
.header h1 {
  margin-bottom: 3rem;
}

.budget-form {
  background-color: #ffffff;
  border-radius: 0.5rem;
  padding: 5rem 3rem;
  box-shadow: 5px 5px 15px 5px hsla(210deg, 4%, 11%, 0.15);
  font-weight: 500;
  color: #316cb9;
  position: absolute;
  left: 0;
  right: 0;
  top: 15rem;
}
@media (min-width: 768px) {
  .budget-form {
    padding: 5rem 10rem;
  }
}
.budget-form .field {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
.budget-form .field label {
  text-align: center;
  font-size: 1.8rem;
  margin-bottom: 5rem;
}
.budget-form .field input:not([type=submit]) {
  padding: 1rem 0;
  line-height: 2rem;
  border: none;
  border-bottom: solid 2px #6794c8;
  outline: none;
  color: #5487c1;
}
.budget-form .field input:not([type=submit])::placeholder {
  color: #437ab9;
}
.budget-form .button-budget {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: #6794c8;
  color: #ffffff;
  transition: 0.3s;
  width: 100%;
}
.budget-form .button-budget:hover {
  background-color: #437ab9;
  cursor: pointer;
}

.control-budget {
  background-color: #ffffff;
  border-radius: 0.5rem;
  padding: 5rem 3rem;
  color: #2d3137;
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 15rem;
}
@media (max-width: 768px) {
  .control-budget {
    display: flex;
    flex-direction: column;
    gap: 5rem;
  }
}
@media (min-width: 768px) {
  .control-budget {
    padding: 3rem 2rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.control-budget .budget-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 2rem;
}
.control-budget .budget-content .danger {
  color: #cb3e25;
}
.control-budget .budget-content .danger span {
  color: #cb3e25;
}
.control-budget .budget-content span {
  color: #3a6bc0;
  font-weight: 500;
}
.control-budget .budget-content .app-reset {
  border: none;
  border-radius: 0.5rem;
  background-color: #6b5abf;
  color: #ffffff;
  padding: 1rem;
  transition: 0.2s;
}
.control-budget .budget-content .app-reset:hover {
  background-color: #5b48b8;
  transform: scale(0.99);
}
.control-budget .budget-chart {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10rem;
}
.control-budget .budget-chart .chart-graphic {
  max-width: 20rem;
}

.main {
  margin-top: 48rem;
}
@media (min-width: 768px) {
  .main {
    margin-top: 25rem;
  }
}

.expenses-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 5rem;
}

.expense {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 1rem;
  padding: 2rem 3rem;
  width: 100%;
  background-color: #ffffff;
}
@media (max-width: 768px) {
  .expense {
    padding: 2rem;
    flex-direction: column;
    gap: 2rem;
  }
}
.expense__content {
  display: flex;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .expense__content {
    flex: 1;
  }
}
@media (max-width: 768px) {
  .expense__content {
    flex-direction: column;
  }
}
.expense__content .category-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 7rem;
  width: 100%;
  max-width: 7rem;
  border-radius: 10rem;
}
.expense__content .category-icon .FAI-icon {
  font-size: 3rem;
  color: #ffffff;
}
.expense__content .category-icon.savings {
  background-color: rgb(197, 115, 197);
}
.expense__content .category-icon.food {
  background-color: rgb(203, 54, 84);
}
.expense__content .category-icon.hygiene {
  background-color: rgb(105, 194, 176);
}
.expense__content .category-icon.house {
  background-color: rgb(98, 92, 162);
}
.expense__content .category-icon.water {
  background-color: rgb(44, 131, 238);
}
.expense__content .category-icon.light {
  background-color: rgb(237, 189, 33);
}
.expense__content .category-icon.internet {
  background-color: rgb(102, 195, 106);
}
.expense__content .category-icon.entertainment {
  background-color: rgb(196, 124, 16);
}
.expense__content .category-icon.clothes {
  background-color: rgb(171, 196, 48);
}
.expense__content .category-icon.health {
  background-color: rgb(225, 74, 55);
}
.expense__content .category-icon.otherExpenses {
  background-color: rgb(129, 61, 197);
}
.expense__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media (min-width: 768px) {
  .expense__description {
    flex: 1;
  }
}
@media (max-width: 768px) {
  .expense__description {
    align-items: center;
  }
}
.expense__category {
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 500;
  color: #316cb9;
}
.expense__name {
  text-align: center;
  font-size: 2rem;
  font-weight: 500;
  color: #316cb9;
}
@media (min-width: 768px) {
  .expense__name {
    text-align: left;
  }
}
.expense__date {
  color: #316cb9;
  font-weight: 500;
}
.expense__date span {
  font-weight: 300;
  color: #58585c;
}
.expense__amount {
  width: 100%;
  max-width: 12rem;
  text-align: right;
  font-size: 2.5rem;
  font-weight: 500;
  color: #4db72f;
}
@media (max-width: 768px) {
  .expense__amount {
    max-width: 100%;
    text-align: center;
  }
}

.swipe-action {
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  font-weight: 500;
  color: #ffffff;
}
.swipe-action__leading {
  background-color: #48b2b4;
}
.swipe-action__trailing {
  background-color: #F45B69;
}

.expense-filter {
  border-radius: 0.5rem;
  background-color: #ffffff;
  padding: 2rem;
  margin-bottom: 3rem;
}
.expense-filter .field {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .expense-filter .field {
    flex-direction: row;
    justify-content: normal;
    gap: 0;
  }
}
.expense-filter .field label {
  font-weight: 500;
  color: #58585c;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .expense-filter .field label {
    font-size: 1.8rem;
    max-width: 20rem;
    width: 100%;
  }
}
.expense-filter .field select {
  border: none;
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: #EBEBEB;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  .expense-filter .field select {
    flex: 1;
  }
}

.alert {
  margin-top: 1rem;
  padding: 2rem 1rem;
  border-radius: 1.5rem;
  text-align: center;
  color: #2d3137;
}
.alert.error {
  border: 0.3rem solid #e99586;
  background-color: #f5cec7;
}

.add-expense {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  background-color: transparent;
}
.add-expense .expense-button {
  color: #F45B69;
  font-size: 6rem;
  transition: 0.2s;
}
.add-expense .expense-button:hover {
  cursor: pointer;
  transform: scale(0.9);
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(21, 21, 23, 0.576);
}
.modal .close-modal {
  color: #ffffff;
  font-size: 4rem;
  position: absolute;
  top: 4rem;
  right: 4rem;
  transition: 0.2s;
}
.modal .close-modal:hover {
  cursor: pointer;
  color: #F45B69;
}

.form-add-expense {
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in;
  opacity: 0;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  background-color: #ffffff;
  padding: 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .form-add-expense {
    padding: 2rem 5rem;
  }
}
.form-add-expense.animation {
  position: relative;
  opacity: 1;
  top: 50%;
  transform: translateY(-50%);
}
.form-add-expense.close {
  position: relative;
  opacity: 0;
  top: 50%;
  transform: translateY(50%);
}
.form-add-expense legend {
  text-align: center;
  font-size: 2rem;
  font-weight: 500;
  color: #3a6bc0;
}
.form-add-expense .field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form-add-expense .field label {
  font-weight: 500;
  color: #3a6bc0;
}
.form-add-expense .field input,
.form-add-expense .field select {
  color: #58585c;
  border: none;
  border-bottom: 1px solid #6c91db;
  outline: none;
  padding: 0.5rem 0;
  line-height: 2rem;
  font-weight: 300;
}
.form-add-expense .field select {
  background-color: transparent;
}
.form-add-expense .button-add-expense {
  border: none;
  border-radius: 0.5rem;
  display: "block";
  padding: 1rem 2rem;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  background-color: #3a6bc0;
  color: #ffffff;
  transition: 0.3s;
  width: 100%;
}
.form-add-expense .button-add-expense:hover {
  background-color: #2e5599;
  cursor: pointer;
}